.header-container
{
    top: 0;
    left: 0;
    display: flex;
    align-items: center;
    /*border-bottom: 1px solid rgb(214, 207, 207);*/
    box-shadow: 0px 15px 10px -15px #111; 
    margin-bottom: 10px;
}
.header
{
    width: 100%;
    height: auto;
    justify-content: space-between;
    align-items: center;
}
.header-left
{
    display: flex;
    flex: 1.5;   
    margin-left: 50px;
    justify-content: flex-start;
}
.header-left span
{
    font-size: 28px;
    padding: 12px;
    text-transform: uppercase;
    font-weight: bold;
    margin-left: 50px;
}
.header-right 
{
    display: flex;
    flex: 1.5;
    align-items: center;
    margin-right: 50px;
    justify-content: flex-end;
}
.header-right nav 
{
    display: flex;
    padding: 12px;
}
.header-right nav a
{
    text-decoration: none;
    color: var(--black-color);
    font-size: 18px;
    margin-right: 30px;
    font-weight: 500;
    transition: 0.7s ease-out;
}
.header-right nav a span 
{
    display: none;
}
.header-right nav a:hover 
{
    color: #e6e600;
    transition: 0.7s ease-out;
}
.icon
{
    display: none;
}

@media screen and (max-width : 768px) {
   .header
   {
        flex-direction: column;
        background-color: rgba(233, 233, 6, 0.9);
        color: var(--grey-color);
        top: 0;
   }
   .header-left
   {
        display: none;
   }
   .header-right
   {
    display: none;

        &.mobile-nav
        {
            display: block;
            margin-left: auto;
            margin-right: auto;
        }
        .mobile-nav a 
        {
            display: flex;
            text-align: center;
            flex-direction: column;
            position: absolute;
            top: 100%;
            left: 0;
            width: 100%;
            color: var(--grey-color);
        }
   }
   .header-right nav 
   {
        flex-direction: column;
        align-items: center;
        text-align: center;
   }
   .header-right nav a
   {
        margin: 10px 0;
        color: var(--black-color);
        padding-bottom: 10px;
        border-bottom: 1px solid #111;
   }
   .header-right nav a:last-child
   {
        border-bottom: none;
   }
   .header-right nav a span 
{
    display: inline;
}
   .header-right nav a:hover 
   {
        color: var(--black-color);
   }
   .icon
   {
        display: block;
        position: absolute;
        top: 20px;
        color: black;
        right: 20px;
        font-size: 22px;
   }
      
}