.email-contianer
{
    width: 50%;
    margin-left: auto;
    margin-right: auto;
    padding: 10px;
    box-shadow: 2px 2px 4px 2px yellow;
    margin-bottom: 50px;
}
.email-contianer:hover
{
    box-shadow: 2px 2px 4px 2px var(--black-color); 
}
.email-contianer a 
{
    text-decoration: none;
    color: white;
    font-family: monospace;
    font-size: 18px;
    cursor: pointer;
}

@media screen and (max-width : 768px) {
    .contact-container
    {
        background-color: var(--dark-grey);
        color: white;    
        height: auto;
    }
    .contact
    {
        padding: 25px;
    }
    .contact.title
    {
        margin-top: 20px;
        font-size: 32px;
        text-transform: uppercase;
        text-align: center;
    }
    .contact-container .text
    {
        margin-top: 10px;
        font-size: 16px;
        text-align: center;
    }
    .email-contianer
    {
        width: 90%;
        margin-left: auto;
        margin-right: auto;
        padding: 10px;
        box-shadow: 4px 4px 4px 2px var(--lemon-yellow);
        margin-bottom: 30px;
    }
    .email-contianer a 
    {
        text-decoration: none;
        color: white;
        font-family: monospace;
        font-size: 16px;
        cursor: pointer;
    }
}