.project-container
{
    background-color: var(--grey-color);
    color: black;
    height: auto;
}
.project
{
    padding: 10px;
}
.project-container .subtitle-container
{
    margin-top: -30px;
    color: var(--black-color);
    margin-bottom: 50px;
}
.project-container .subtitle
{
    font-size: 18px;
}
 .project-detail-container
 {
    height: auto;
    display: block;
    background-color : rgba(0, 0, 0, 0.7);
    color: white;
    margin-top: -30px;
    margin-left: auto;
    margin-right: auto;
    padding: 0px;
}
 .project-item
 {
    display: flex;
    flex-direction: row;
 }
 .project-image-container
 {
    flex:2;
    margin: 10px;
 }
 .project-image-container img 
 {
    width: 90%;
    height: 90%;
    margin: 20px;
    border-radius: 10px;
    box-shadow: 4px 2px 8px 2px black;
    transition : all 0.7s ease-in-out;
 }
 .project-image-container img:hover 
 {
    transform: scale(1.7);
    transform-origin: left center;
 }
.project-detail
{
    flex: 1;
    display: flex;
    padding-left: 10px;
    padding-right:50px ;
    align-items: center;
    justify-content: center;
    margin-top: 60px;
    margin-bottom: 20px;
}
.project-detail .description .text
{
    color: white;
}
.tools
{
    display: block;
}

.tools ul 
{
    display: block;
    padding: 0px;
}
.tools ul li 
{
    list-style: none;
    display: inline-block;
    padding: 10px;
    background-color: var(--lemon-yellow);
    color: black;
    margin: 10px;
    margin-left: 0px;
    width: auto;
    border-radius: 8px;
}

@media screen and (max-width: 768px) {
    .project-container
    {
        background-color: var(--grey-color);
        color: black;
        height: auto;
    }
    .project
    {
        padding: 25px;
    }
    .project .title
    {
        margin-top: 20px;
        font-size: 32px;
        text-transform: uppercase;
        text-align: center;
    }
    .project-container .subtitle-container
    {
        margin-top: -20px;
        color: var(--black-color);
    }
    .project-container .subtitle
    {
        font-size: 16px;
    }
    .project-detail-container
    {
       height: auto;
       width: 100%;
       display: block;
       background-color : rgba(0, 0, 0, 0.7);
       color: white;
    }

    .project-item
    {
       display: block;
       padding: 10px;
    }
    .project-image-container
    {
       display: block;
       margin:10px;
    }
    .project-image-container img 
    {
       width: 100%;
       height: 100%;
       margin: 0px;
       border-radius: 5px;
       box-shadow: 4px 2px 8px 2px black;
    }
    .project-image-container img:hover 
    {
       transform: scale(1);
    }
   .project-detail
   {
       flex: 1;
       display: block;
       margin-top: 60px;
       margin-bottom: 20px;
   }
   .project-detail .description .text
   {
       color: white;
   }
   .tools
   {
       display: block;
   }
   .tools ul 
   {
       display: block;
       padding: 0px;
   }
   .tools ul li 
   {
       list-style: none;
       display: inline-block;
       padding: 10px;
       background-color: var(--lemon-yellow);
       color: black;
       margin: 10px;
       margin-left: 0px;
       width: auto;
       border-radius: 5px;
   }
   

    
}