.aboutme-container
{
    height: auto;
    background-color: var(--black-color);
    color: white;
    margin-top: 10px;
}
.aboutme
{
    padding: 10px;
    margin-left: auto;
    margin-right: auto;
}
.title
{
    font-size: 54px;
    text-transform: uppercase;

}
.subtitle-container
{
    margin-top: -30px;
    color: var(--dark-grey);
}
.subtitle
{
    font-size: 18px;
}
.aboutme-detail
{
    display: flex;
    justify-content: center;
    align-items: center;
}
.detail 
{
    width: 50%;
    flex: 2;
    margin-top: -50px;

}
.detail .header-text 
{
    font-size: 28px;
}
.detail .text 
{
    font-size: 16px;
    color: var(--grey-color);
    margin-left: 0px;

}
.skill7,.skill8, .skill9, .skill10,.skill11,.skill12,.skill13,.skill14,.skill15,.skill16
{
    background-color: var(--lemon-yellow);
    padding: 10px;
    border: none;
    margin-right: 10px;
    border-radius: 4px;
    color: var(--black-color);
    font-weight: bold;
    font-size: 16px;
    width: auto;
    display: inline-block;
    margin-bottom: 10px;
}
.cube-container
{
    width: 40%;
    flex: 1;
    height: 50vh;
    padding: 13%;
    margin-left: 100px;
    right: 0%;
    margin-top: 100px;
   
}
.cubespinner
{
    animation-name: spincube;
    animation-timing-function: ease-in-out;
    animation-iteration-count: infinite;
    animation-duration: 13s;
    transform-style: preserve-3d;
    transform-origin: 100px 100px 0;
}
.cubespinner div 
{
    width: 300px;
    height: 300px;
    position: absolute;
    background-color: rgba(0, 0, 0, 0.9);
    border: 1px solid var(--lemon-yellow);
    box-shadow: 0 0 20px 0px var(--green-color);
    text-align: center;
    color: var(--white-color);
    font-size: 46px;
    display: flex;
    justify-content: center;
    align-items: center;
}
.skill1
{
    transform: translateZ(150px);
}
.skill2
{
    transform: rotateY(90deg) translateZ(150px);
}
.skill3
{
    transform: rotateY(90deg) rotateX(90deg) translateZ(150px);
}
.skill4
{
    transform: rotateY(180deg) rotateZ(90deg) translateZ(150px);
}
.skill5
{
    transform: rotateY(-90deg) rotateZ(90deg) translateZ(150px);
}
.skill6
{
    transform: rotateX(-90deg) translateZ(150px);
}
@keyframes spincube
{
    from,
    to{
        transform: rotateX(0deg) rotateY(0deg) rotateZ(0deg);
    }
    16%{
        transform: rotateY(-90deg) rotateZ(90deg);
    }
    33%{
        transform: rotateY(-90deg) rotateX(90deg);
    }
    50%{
        transform: rotateY(-180deg) rotateZ(90deg);
    }
    66%{
        transform: rotateY(-270deg) rotateX(90deg);
    }
    83%{
        transform: rotateX(90deg);
    }
}

@media screen and (max-width: 768px) {
    .aboutme-container
    {
        height: auto;
        background-color: var(--black-color);
        color: white;
    }
    .aboutme
    {
        padding: 25px;
    }
    .title
    {
        margin-top: 20px;
        font-size: 32px;
        text-transform: uppercase;
        text-align: center;
    }
    .subtitle-container
    {
        margin-top: -10px;
        color: var(--dark-grey);
    }
    .subtitle
    {
        font-size: 16px;
        text-align: center;
    }
    .aboutme-detail
    {
        display: block;
    }
    .detail 
    {
        width: 100%;
        text-align: left;
    }
    .detail .header-text 
    {
        font-size: 22px;
        text-align: center;
        margin-top: 75px;
    }
    .detail .text 
    {
        font-size: 16px;
        color: var(--grey-color);
    }
    .cube-container {
        display: none;
        width: 80%; /* Adjust the width as needed */
        margin: 0 auto; /* Center the block horizontally */
        height: auto; /* Adjust the height as needed */
        padding: 20px; /* Adjust the padding as needed */
      }
    
      /* Remove the animation for mobile devices */
      .cubespinner {
        animation: none;
      }
    .skill7,.skill8, .skill9, .skill10,.skill11,.skill12,.skill13,.skill14,.skill15,.skill16
    {
        padding: 15px;
        font-weight: bold;
        font-size: 18px;
        display: inline-block;
        justify-content: center;
        margin-left: auto;
        margin-right: auto;
        align-items: center;
        margin-bottom: 8px;
        background-color: transparent;
        color: white;
        filter: drop-shadow(4px 2px 4px var(--lemon-yellow));
    }
    .skill7
    {
        color: white;
        filter: drop-shadow(4px 2px 4px var(--lemon-yellow));
    }
    .skill8
    {
        color: white;
        filter: drop-shadow(4px 2px 4px #ff80df);
    }
    .skill10 
    {
        color: white;
        filter: drop-shadow(4px 2px 4px #e65c00);
    }
    .skill11
    {
        color: white;
        filter: drop-shadow(4px 2px 4px #e65c00);
    }
    .skill12
    {
        color: white;
        filter: drop-shadow(4px 2px 4px #6666ff);
    }
    .skill13
    {
        color: white;
        filter: drop-shadow(4px 2px 4px #4da6ff);
    }
    .skill14
    {
        color: white;
        filter: drop-shadow(4px 2px 4px #f7e560);
    }
    .skill15
    {
        color: white;
        filter: drop-shadow(4px 2px 4px #4ecef8);
    }
    .skill16
    {
        color: white;
        filter: drop-shadow(4px 2px 4px #afaff7);
    }
    
}