.home-container
{
    height: auto;
    background-color: var(--grey-color);
    color: black;
}
.home
{
    padding: 100px;
}
.name
{
    font-size: 54px;
    text-transform: uppercase;
    margin-top: -20px;
}
.designation
{
    font-size: 16px;
    margin-top: -40px;
    color: var(--dark-grey);
    margin-left: auto;
    margin-right: auto;
}
.text-container .text
{
    font-size: 22px;
    width: 90%;
    text-align: center;
}
.project-button-contianer
{
    background-color: var(--lemon-yellow);
    width: 20%;
    height: auto;
    margin-left: auto;
    margin-right: auto;
    margin-top: 40px;
    padding: 20px;
    border-radius: 10px;
    margin-bottom: 10px;

    display: block;
    background: linear-gradient(to right, black 50%, var(--lemon-yellow) 50%);
    background-size: 200% 100%;
    background-position: right bottom;
    transition: all .5s ease-out;
}
.project-button-contianer:hover{
    background-position: left bottom;
    color: white;
}
.project-button-contianer a
{
    font-size: 20px;
    text-decoration: none;
    color: var(--black-color);
    font-weight: bold;
    transition: all .6s ease-out;
    display: block;
    text-align: center;
    line-height: auto;

}
.project-button-contianer a:hover{
    color: white;
}
.project-button-contianer:hover 
{
    box-shadow: 3px 3px 3px 3px var(--dark-grey);
}

@media  screen and (max-width: 768px) {
    .home-container
        {
            height: auto;
            background-color: var(--grey-color);
            color: black;    
        }
        .home
        {
            padding: 30px;
            margin-top: -10px;
            margin-bottom: -10px;
        }
        .name
        {
            font-size: 32px;
            text-transform: uppercase;
            margin-left: auto;
            margin-right: auto;
            margin-top: 50px;
            text-align: center;
        }
        .designation
        {
            font-size: 14px;
            margin-top: -15px;
            color: var(--dark-grey);
            margin-left: auto;
            margin-right: auto;
        }
        .text-container .text
        {
            font-size: 16px;
            width: 100%;
            text-align: center;
        }
        .project-button-contianer
        {
            background-color: var(--lemon-yellow);
            width: 30%;
            height: auto;
            margin-left: auto;
            margin-right: auto;
            margin-top: 20px;
            padding: 15px;
            border-radius: 5px;
            margin-bottom: 40px;
        }
        .project-button-contianer a
        {
            font-size: 16px;
            text-decoration: none;
            color: var(--black-color);
            font-weight: bold
        }
}