.footer-container
{
    height: auto;
    background-color: black;
    color: white;
}
.footer
{
    padding: 70px;
    display: flex;
}
.footer-left
{
    width: 50%;
    height: auto;
}
.footer-left .footer-name
{
    color: var(--grey-color);
    font-size: 32px;
}
.footer-left .footer-text
{
    width: 70%;
    font-size: 14px;
    color: var(--dark-grey);
}
.footer-center
{
    width: 20%;
    height: auto;
    margin-right: 20px;
}
.links span
{
    color: var(--grey-color);
    font-size: 28px;
}
.quick-links
{
    margin-top: 8px;
}
.quick-links a
{
    display: block;
    text-decoration: none;
    color: var(--dark-grey);
    margin-bottom: 8px;
    font-size: 14px;    
}
.quick-links a:hover
{
    color: var(--lemon-yellow);
}
.footer-right
{
    width: 30%;
    height: auto;
    margin-left: 40px;
}
.footer-right .social
{
    font-size: 28px;
    color: var(--grey-color);
}
.footer-right .social-icon
{
    margin-top: 10px;
}
.footer-right .social-icon a
{
    font-size: 32px;
    margin-right: 15px;
    color: var(--dark-grey);
}
.footer-right .social-icon a:hover
{
    color: var(--lemon-yellow);
}
.horizontal-line
{
    height: 1px;
    margin-left: 70px;
    margin-right: 70px;
    background-color: rgb(58, 58, 58);
    margin-bottom: 10px;
}
.copy-right
{
    padding-bottom: 10px;
}
.copy-right span
{
    font-size: 12px;
    color: grey;
}

@media screen and (max-width:768px) {
    .footer-container
    {
        height: auto;
        background-color: black;
        color: white;
        text-align: center;
    }
    .footer
    {
        padding: 25px;  
        display: block; 
    }
    .footer-left
    {
        width: 100%;
        height: auto;
        margin-top: 20px;
    }
    .footer-left .footer-name
    {
        color: var(--grey-color);
        font-size: 24px;
    }
    .footer-left .footer-text
    {
        width: 100%;
        font-size: 16px;
    }
    .footer-center
    {
        width: 100%;
        height: auto;
        margin-top: 40px;
    }
    .links span
    {
        color: var(--grey-color);
        font-size: 24px;
    }
    .quick-links
    {
        margin-top: 8px;
    }
    .quick-links a
    {
        display: block;
        text-decoration: none;
        color: var(--dark-grey);
        margin-bottom: 8px;
        font-size: 16px;    
    }
    .quick-links a:hover
    {
        color: var(--lemon-yellow);
    }
    .footer-right
    {
        width: 100%;
        height: auto;
        margin-top: 40px;
        margin-left: 0px;
    }
    .footer-right .social
    {
        font-size: 24px;
    }
    .footer-right .social-icon
    {
        margin-top: 10px;
        margin-left: 10px;
    }
    .footer-right .social-icon a
    {
        font-size: 24px;
        padding: 4px;
        color: var(--dark-grey);
    }
    .footer-right .social-icon a:hover
    {
        color: var(--green-color);
    }
    .horizontal-line
    {
        height: 1px;
        margin-left: 10px;
        margin-right: 10px;
        background-color: rgb(58, 58, 58);
        margin-bottom: 10px;
    }
    .copy-right
    {
        padding-bottom: 10px;
    }
    .copy-right span
    {
        font-size: 12px;
        color: grey;
    }
}